h1 {
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background: #fff;
  margin: 0;
}

.main {
  margin: 227px 0 18px;
}

@media (width >= 768px) {
  .main {
    margin-top: 28px;
  }
}

h1, h2 {
  font-family: Montserrat, sans-serif;
  font-weight: 700;
}

h3, input {
  font-family: Montserrat, sans-serif;
  font-weight: 400;
}

p {
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 300;
}

span, a, button {
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 500;
}

div {
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 700;
}

p, h1, h2, h3, button, ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

a, button {
  cursor: pointer;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.contacts {
  background-image: url("footer.cfec4fe7.png");
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 386px;
  padding: 88px 0;
}

@media (width >= 1280px) {
  .contacts {
    height: 550px;
    padding: 163px 0;
  }
}

.form {
  color: #fff;
}

.form__title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 36px;
}

@media (width >= 768px) {
  .form__title {
    font-size: 48px;
  }
}

.form__description {
  text-align: center;
  grid-column: 1 / -1;
  font-size: 18px;
  line-height: 150%;
}

.form__input {
  justify-content: center;
  margin-top: 50px;
  display: flex;
}

.form__data {
  width: 100%;
  height: 50px;
  padding-inline: 24px;
  font-size: 14px;
}

@media (width >= 768px) {
  .form__data {
    width: 70%;
    height: 70px;
  }
}

@media (width >= 1280px) {
  .form__data {
    width: 40%;
    height: 70px;
  }
}

.container {
  padding-inline: 20px;
}

@media (width >= 768px) {
  .container {
    padding-inline: 39px;
  }
}

@media (width >= 1280px) {
  .container {
    padding-inline: 55px;
  }
}

.events {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  gap: 60px 20px;
  padding-top: 70px;
  display: grid;
}

@media (width >= 768px) {
  .events {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .events {
    --columns: 12;
    row-gap: 104px;
  }
}

.events__title {
  text-align: center;
  grid-column: 1 / -1;
  width: 300px;
  margin: 0 auto;
  font-size: 36px;
}

@media (width >= 768px) {
  .events__title {
    text-align: left;
    width: 100%;
    font-size: 48px;
  }
}

@media (width >= 1280px) {
  .events__title {
    padding-top: 200px;
    font-size: 56px;
  }
}

.event {
  --columns: 2;
  grid-column: 1 / -1;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 768px) {
  .event {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .event {
    --columns: 12;
  }
}

.event--full-screen {
  background-image: url("romantic.c069a573.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 210px;
  padding-inline: 0;
}

@media (width >= 768px) {
  .event--full-screen {
    height: 390px;
  }
}

@media (width >= 1280px) {
  .event--full-screen {
    height: 550px;
  }
}

.event__image {
  object-fit: cover;
  grid-column: 1 / 7;
  width: 100%;
  height: 210px;
  margin-bottom: 20px;
}

@media (width >= 768px) {
  .event__image {
    height: 520px;
    margin-bottom: 40px;
  }
}

@media (width >= 1280px) {
  .event__image {
    grid-column: 1 / 8;
    margin-bottom: 0;
  }
}

.event__text {
  grid-column: 1 / 7;
  height: fit-content;
}

@media (width >= 768px) {
  .event__text {
    grid-column: 1 / 5;
  }
}

@media (width >= 1280px) {
  .event__text {
    grid-column: 9 / 13;
    margin: auto;
  }
}

.event__text--full-screen {
  text-align: center;
  background-color: #fff;
  margin-inline: 20px;
  padding-block: 20px;
  position: relative;
  top: 153px;
  box-shadow: 0 2px #1a5a4c40;
}

@media (width >= 768px) {
  .event__text--full-screen {
    text-align: left;
    grid-column: 1 / -1;
    width: 570px;
    margin: 0 auto;
    padding: 50px 55px;
    top: 306px;
  }
}

@media (width >= 1280px) {
  .event__text--full-screen {
    top: 448px;
  }
}

.event__place {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.event__place--full-screen {
  padding-inline: 20px;
}

@media (width >= 768px) {
  .event__place--full-screen {
    padding-inline: 0;
  }

  .event__place {
    justify-content: flex-start;
    gap: 24px;
    font-size: 14px;
  }
}

.event__type {
  color: #687480;
}

.event__dates {
  color: #1a5a4c;
  font-weight: 700;
  line-height: 150%;
}

.event__title {
  color: #0f0e08;
  align-items: center;
  gap: 15px;
  margin-block: 10px;
  font-size: 28px;
  display: flex;
}

@media (width >= 768px) {
  .event__title {
    font-size: 36px;
  }
}

.event__point {
  background-color: #f14a27;
  width: 10px;
  height: 10px;
}

.event__description {
  color: #4e4e4e;
  line-height: 150%;
}

.footer {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-block: 50px;
  display: grid;
}

@media (width >= 768px) {
  .footer {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .footer {
    --columns: 12;
  }
}

.footer__links {
  grid-column: 1 / -1;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

@media (width >= 768px) {
  .footer__links {
    flex-direction: column;
    grid-column: 1 / 1;
    gap: 62px;
    margin-bottom: 0;
  }
}

.footer__social {
  gap: 20px;
  display: flex;
}

.footer__line {
  background-color: #1a5a4c;
  grid-column: 1 / -1;
  width: 100%;
  height: 1px;
  margin: 20px 0 40px;
}

@media (width >= 768px) {
  .footer__line {
    grid-column: 2 / 6;
  }
}

@media (width >= 1280px) {
  .footer__line {
    grid-area: 1 / 7;
    justify-self: center;
    width: 1px;
    height: 100%;
  }
}

.move-top {
  background-image: url("arrow-up.ebb7807b.png");
  background-position: center;
  background-repeat: no-repeat;
  border: 1px solid #1a5a4c;
  border-radius: 50%;
  grid-column: 2;
  justify-self: end;
  width: 50px;
  height: 50px;
  transition: all .3s;
}

.move-top:hover {
  transition: all .3s;
  transform: scale(1.1);
}

.move-top--a {
  width: 50px;
  height: 50px;
  display: block;
}

@media (width >= 768px) {
  .move-top {
    grid-column: 5;
  }
}

@media (width >= 1280px) {
  .move-top {
    grid-column: 12;
  }
}

.rights {
  text-align: center;
  color: #0f0e08;
  flex-direction: column;
  grid-column: 1 / -1;
  align-items: center;
  gap: 10px;
  margin-top: 80px;
  font-size: 12px;
  display: flex;
}

@media (width >= 768px) {
  .rights {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}

.rights__copy, .rights__design {
  font-weight: 300;
}

.gallery {
  height: 820px;
  margin: 194px 0 100px;
  overflow: hidden;
}

@media (width >= 768px) {
  .gallery {
    margin-top: 244px;
  }
}

@media (width >= 1280px) {
  .gallery {
    height: auto;
  }
}

.gallery__title {
  text-align: center;
  margin-bottom: 40px;
  font-size: 36px;
}

@media (width >= 768px) {
  .gallery__title {
    font-size: 48px;
  }
}

@media (width >= 1280px) {
  .gallery__slider {
    display: none;
  }
}

.gallery__photos {
  display: none;
}

@media (width >= 1280px) {
  .gallery__photos {
    flex-direction: column;
    gap: 30px;
    display: flex;
  }
}

.gallery__top {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 768px) {
  .gallery__top {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .gallery__top {
    --columns: 12;
  }
}

.gallery__top--left {
  width: 100%;
}

@media (width >= 768px) {
  .gallery__top--left {
    grid-column: 1 / 5;
  }
}

@media (width >= 1280px) {
  .gallery__top--left {
    grid-column: 1 / 5;
  }
}

.gallery__top--right {
  width: 100%;
}

@media (width >= 768px) {
  .gallery__top--right {
    grid-column: 5 / 13;
  }
}

@media (width >= 1280px) {
  .gallery__top--right {
    grid-column: 5 / 13;
  }
}

.gallery__bottom {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 768px) {
  .gallery__bottom {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .gallery__bottom {
    --columns: 12;
  }
}

.gallery__bottom--left {
  width: 100%;
}

@media (width >= 768px) {
  .gallery__bottom--left {
    grid-column: 1 / 9;
  }
}

@media (width >= 1280px) {
  .gallery__bottom--left {
    grid-column: 1 / 9;
  }
}

.gallery__bottom--right {
  width: 100%;
}

@media (width >= 768px) {
  .gallery__bottom--right {
    grid-column: 9 / 13;
  }
}

@media (width >= 1280px) {
  .gallery__bottom--right {
    grid-column: 9 / 13;
  }

  .gallery__img {
    width: 100%;
    transition-duration: 1s;
  }

  .gallery__img:hover {
    opacity: 1;
    z-index: 10;
    background: #eee;
    border-radius: 10px;
    transform: scale(1.05);
  }
}

.header {
  padding-top: 9px;
  position: relative;
}

.header__top {
  align-items: center;
  display: flex;
  position: relative;
}

.header__photo {
  background-image: url("museum.bbf904e8.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 300px;
  position: absolute;
  top: 0;
}

@media (width >= 768px) {
  .header__photo {
    width: 50%;
    height: 374px;
    right: 0;
  }
}

@media (width >= 1280px) {
  .header__photo {
    width: 56%;
    height: 655px;
  }
}

.header__communication {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin: 20px auto;
  display: grid;
  position: relative;
  top: 226px;
}

@media (width >= 768px) {
  .header__communication {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1280px) {
  .header__communication {
    --columns: 12;
  }
}

@media (width >= 768px) {
  .header__communication {
    margin: 0;
    top: 0;
  }
}

.header__title {
  text-align: center;
  grid-column: 1 / -1;
  margin-bottom: 20px;
  font-size: 45px;
}

@media (width >= 768px) {
  .header__title {
    text-align: left;
    grid-column: 1 / 4;
    margin: 40px 0 38px;
    font-size: 54px;
    line-height: 120%;
  }
}

@media (width >= 1280px) {
  .header__title {
    grid-column: 1 / 6;
    margin: 119px 0 89px;
    font-size: 72px;
  }
}

.header__events {
  display: none;
}

@media (width >= 1280px) {
  .header__events {
    color: #687480;
    text-transform: uppercase;
    writing-mode: vertical-rl;
    grid-area: 3 / 1 / 3 / 1;
    align-items: baseline;
    gap: 20px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 14px;
    font-weight: 700;
    display: flex;
    position: relative;
    bottom: 15px;
    transform: rotate(180deg);
  }

  .header__events:before {
    content: "";
    background-color: #1a5a4c;
    width: 1px;
    height: 89px;
    display: block;
  }
}

.header__button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #1a5a4c;
  grid-column: 1 / -1;
  width: 100%;
  padding-right: 50px;
  display: flex;
  position: relative;
}

@media (width >= 768px) {
  .header__button {
    grid-column: 1 / 3;
    width: 270px;
    height: 70px;
  }
}

@media (width >= 1280px) {
  .header__button {
    grid-column: 3 / 6;
  }
}

.header__buy {
  text-transform: uppercase;
  margin: auto;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
}

.hours {
  flex-direction: column;
  gap: 16px;
  font-size: 14px;
  display: flex;
}

@media (width >= 768px) {
  .hours {
    flex-direction: row;
    gap: 26px;
  }

  .hours--footer {
    flex-direction: column;
    grid-column: 2 / 4;
  }
}

@media (width >= 1280px) {
  .hours--footer {
    grid-column: 3 / 5;
  }
}

.hours__shedule {
  font-weight: 400;
}

.hours__work {
  text-wrap: nowrap;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.hours__holiday {
  font-weight: 400;
}

@media (width >= 768px) {
  .hours__holiday {
    margin-top: 38px;
  }
}

.hours__item {
  font-weight: 400;
}

.hours__time {
  font-weight: 600;
  line-height: 150%;
}

.icon {
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
  transition: all .3s;
  display: block;
}

.icon:hover {
  transition: all .3s;
  transform: scale(1.1);
}

.icon--menu {
  background-image: url("menu.ac307ddf.png");
  position: absolute;
}

@media (width >= 768px) {
  .icon--menu {
    margin-right: 190px;
    position: relative;
  }
}

.icon--close {
  background-image: url("close.28fa339c.png");
  position: absolute;
}

@media (width >= 768px) {
  .icon--close {
    margin-right: 190px;
    position: relative;
  }
}

.icon--logo {
  background-image: url("NAMU-logo.f03f3403.png");
  width: 55px;
  height: 61px;
  margin: auto;
}

@media (width >= 768px) {
  .icon--logo {
    width: 72px;
    height: 72px;
  }
}

.icon--footer {
  margin: 0;
}

.icon--facebook {
  background-image: url("facebook.c14a414f.png");
  margin: auto;
}

.icon--instagram {
  background-image: url("instagram.79c963dc.png");
  margin: auto;
}

.menu {
  z-index: 1;
  color: #fff;
  width: 100vw;
  font-family: IBM Plex Sans, sans-serif;
  font-weight: 400;
  overflow: auto;
}

.menu__green {
  background-color: #1a5a4c;
  flex-direction: column;
  justify-content: space-between;
  height: max-content;
  padding-block: 27px;
  display: flex;
}

@media (width >= 768px) {
  .menu__green {
    flex-direction: row;
  }
}

.menu__dark {
  opacity: .3;
  background-color: #0f0e08;
  height: 100vh;
}

.menu__nav--footer {
  grid-column: 1 / -1;
  display: grid;
}

@media (width >= 1280px) {
  .menu__nav--footer {
    grid-area: 1 / 8 / 2 / 13;
  }
}

.menu__list {
  gap: 28px;
  padding-left: 52px;
  font-weight: 500;
  display: flex;
}

.menu__list--footer {
  grid-column: 1 / -1;
  width: 100%;
  padding-left: 0;
}

@media (width >= 768px) {
  .menu__list--footer {
    --columns: 2;
    grid-template-columns: repeat(var(--columns), 1fr);
    column-gap: 20px;
    display: grid;
  }
}

@media (width >= 768px) and (width >= 768px) {
  .menu__list--footer {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 768px) and (width >= 1280px) {
  .menu__list--footer {
    --columns: 12;
  }
}

.menu__left {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.menu__left--footer {
  grid-column: 2 / 4;
  gap: 20px;
}

@media (width >= 1280px) {
  .menu__left--footer {
    grid-column: 1 / 5;
  }
}

.menu__right {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.menu__right--footer {
  grid-column: 4;
  gap: 20px;
}

@media (width >= 1280px) {
  .menu__right--footer {
    grid-column: 6 / 13;
  }
}

.menu__line {
  background-color: #fff;
  width: 100%;
  height: 1px;
  margin-block: 24px;
}

@media (width >= 768px) {
  .menu__line {
    width: 1px;
    height: 18vh;
    margin-block: 0;
    margin-inline: 38px;
  }
}

.menu__link {
  position: relative;
}

.menu__link:after {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  width: 100%;
  height: 1px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -3px;
  transform: scale(0);
}

.menu__link:hover:after {
  transform: scale(1);
}

.menu__link--footer {
  color: #0f0e08;
  font-size: 14px;
  font-weight: 400;
}

.menu__link--footer:after {
  background-color: #0f0e08;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0);
}

.page.target-active {
  overflow: hidden;
}

.arrow {
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  width: 50px;
  height: 50px;
  transition: all .3s;
  position: absolute;
  right: 0;
}

.arrow:hover {
  background-position: 100%;
}

.arrow--header {
  background-color: #1a5a4c;
  background-image: url("arrow-white.06416a60.png");
}

.arrow--red {
  background-color: #f14a27;
  background-image: url("arrow-black.cc07b0af.png");
  flex-shrink: 0;
  position: static;
}

@media (width >= 768px) {
  .arrow {
    width: 70px;
    height: 70px;
  }
}

.address {
  font-size: 14px;
}

@media (width >= 768px) {
  .address {
    grid-column: 4 / 7;
  }
}

@media (width >= 1280px) {
  .address {
    grid-column: 5 / 7;
  }
}

.address__title {
  margin-bottom: 18px;
  font-weight: 400;
  line-height: 150%;
}

.address__contacts {
  font-weight: 400;
  line-height: 150%;
}

.address__link {
  color: #0f0e08;
  position: relative;
}

.address__link:after {
  content: "";
  transform-origin: 0;
  background-color: #0f0e08;
  width: 100%;
  height: 1px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -3px;
  transform: scale(0);
}

.address__link:hover:after {
  transform: scale(1);
}

.slider {
  border-radius: 10px;
  width: 800px;
  height: 500px;
  overflow: hidden;
}

.slides {
  width: 110%;
  display: flex;
}

.slides input {
  display: none;
}

.slide {
  object-fit: contain;
  width: 100%;
  margin: 0 25px;
  transition: all 2s;
}

@media (width >= 768px) {
  .slide {
    width: 45%;
  }
}

.slide img {
  width: 800px;
  height: 100%;
  margin: 0 20px;
}

.navigation-manual {
  justify-content: center;
  display: flex;
  position: relative;
  top: 20px;
}

.manual-btn {
  cursor: pointer;
